import React, { Component } from "react";
import { baseUrl, key } from "./config";
import _ from "lodash";
import axios from "axios";
import DragScrollProvider from "drag-scroll-provider";

// Font Awesome
import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { fab } from "@fortawesome/free-brands-svg-icons";
library.add(fab, fas);

class Imovel extends Component {
  state = {
    imovel: {
      Foto: [{ Foto: "", FotoPequena: "" }]
    },
    currentPhoto: 0,
    fields: [
      "Codigo",
      "Categoria",
      "Bairro",
      "ValorVenda",
      "ValorLocacao",
      "AreaTotal",
      "AreaPrivativa",
      "Dormitorios",
      "Vagas",
      "Status",
      "Caracteristicas",
      "InfraEstrutura",
      "Latitude",
      "Longitude",
      "FotoDestaque",
      { Foto: ["Foto", "FotoPequena", "Destaque"] }
    ]
  };
  getImovel = async codigo => {
    try {
      const fields = JSON.stringify(this.state.fields);
      const getImovel = await axios.get(
        `${baseUrl}/imoveis/detalhes?key=${key}&imovel=${codigo}&pesquisa={"fields":${fields}}`
      );
      const imovel = getImovel.data;
      const fotos = _.values(imovel.Foto);

      imovel.Foto = _.sortBy(fotos, function(foto) {
        return foto.Destaque === "Sim" ? 0 : 1;
      });
      console.log(imovel);
      this.setState({ imovel });
    } catch (error) {
      alert("Não tem imóvel");
    }
  };
  componentDidMount() {
    this.getImovel(this.props.match.params.codigo);
  }
  _handlePhotoSelect = foto => {
    const fotos = this.state.imovel.Foto;

    const currentPhoto = fotos.indexOf(foto);
    this.setState({ currentPhoto });
  };
  _handlePhotoChange = direction => {
    const numberOfPhotos = this.state.imovel.Foto.length - 1;
    let currentPhoto = this.state.currentPhoto;
    console.log(numberOfPhotos);
    if (direction === "next") {
      if (currentPhoto === numberOfPhotos) {
        currentPhoto = 0;
      } else {
        currentPhoto++;
      }
    } else {
      if (currentPhoto === 0) {
        currentPhoto = numberOfPhotos;
      } else {
        currentPhoto--;
      }
    }
    this.setState({ currentPhoto });
  };
  render() {
    const { imovel, currentPhoto } = this.state;
    return (
      <div className="imovel">
        <div className="container">
          <div className="row">
            <div className="col-xl-12" />
          </div>
          <div className="row">
            <div className="col-xl-8">
              <div className="imovel__fotoDestaque">
                <div
                  className="imovel_box__imagem-previous"
                  onClick={() => this._handlePhotoChange("previous")}
                >
                  <FontAwesomeIcon icon={["fas", "chevron-left"]} />
                </div>
                <div
                  className="imovel_box__imagem-next"
                  onClick={() => this._handlePhotoChange("next")}
                >
                  <FontAwesomeIcon icon={["fas", "chevron-right"]} />
                </div>
                <img src={imovel.Foto[currentPhoto].Foto} alt="" />
              </div>
              <DragScrollProvider>
                {({ onMouseDown, ref }) => (
                  <div
                    className="imovel__slider"
                    ref={ref}
                    onMouseDown={onMouseDown}
                  >
                    {imovel.Foto.map((foto, i) => {
                      return (
                        <div
                          onClick={() => this._handlePhotoSelect(foto)}
                          className="imovel__slider_slide"
                        >
                          <img
                            className={currentPhoto === i ? "active" : ""}
                            src={foto.FotoPequena}
                          />
                        </div>
                      );
                    })}
                  </div>
                )}
              </DragScrollProvider>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Imovel;
